* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 1rem;
}

body {
  min-height: 100vh;
  font-family: "arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  background-color: #efefef;
}

#root {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  width: 100%;
  max-width: 100%;
  flex-grow: 1;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid mediumblue;
  box-shadow: 0px 0px 15px gray;
  margin: auto;
}

.Header,
.Footer {
  width: 100%;
  background-color: #66d8f5;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.Header h1 {
  font-size: 1.5rem;
}

.Header svg {
  font-size: 2rem;
}

section {
  border: 1px solid blue;
  flex: 1;
  overflow-y: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: 0;
  text-align: center;
  background-color: whitesmoke;
}

footer {
  width: 100%;
  padding: 0.25em;
  background-color: #66d8f5;
  color: black;
  display: grid;
  place-content: center;
}

.box-effect {
  box-shadow: 0 12px 12px rgba(0,0,0,0.5);
  padding: 25px;
}

img{
  border-radius: 50%;
  width: 200px;
  height: 200px;
}